import React, { Component, Suspense } from 'react';
import DisplayUrl from '../../../PublishCommonComponents/DisplayUrl';
import { DisplayURL } from '../../../../../Services/Api'
class OnSignUrl extends Component {

    render() {
        const { data } = this.props
        return  <div className="card text-decoration-none">
                    <div className="card-body p-lg-7">
                        <div className="m-auto">
                            <div className="embed_code">
                                <h5 className="mb-5 fw-bolder">Steps To Display Social wall With OnSign.tv</h5>
                                <h6>Step 1 : Copy The Display Wall URL</h6>
                                <DisplayUrl url={`${DisplayURL}${data && Object.keys(data).length > 0 ? data.wallId : null}`}/> 
                                <h6 className="mb-5">Step 2: Add The Social Wall To OnSign.tv's App In The Following Way</h6>
                                <ul className="list-unstyled list-points fs-7">
                                    <li className="mb-3">Start by Logging in to your <span className="fw-bold">onsign.tv account</span> and click on <span className="fw-bold">New</span></li>
                                    <li className="mb-3">Here, select <span className="fw-bold">App</span> and go to the <span className="fw-bold">Social Networks</span> option</li>
                                    <li className="mb-3">Tap on 'Taggbox Display' to add a social wall</li>
                                    <li className="mb-3">Next, paste the copied URL in the <span className="fw-bold">URL field</span> and click on save.</li>
                                    <li className="mb-3">Congratulations, you've successfully added social wall in OnSign.tv</li>
                                </ul>
                            </div>
                        </div>
                    </div>
                </div>
    }
}

export default OnSignUrl;